/* ═══════════════════════════════════════════════════════════
   Physics IDE — Dual Theme (Light + Dark)
   Uses CSS custom properties switched via [data-theme]
   ═══════════════════════════════════════════════════════════ */

/* ── Dark theme (default) ──────────────────────────────── */
:root,
[data-theme="dark"] {
  --bg-base:      #1e1e2e;
  --bg-surface:   #181825;
  --bg-overlay:   #11111b;
  --bg-mantle:    #1a1a2e;
  --bg-card:      #1e1e2e;
  --bg-card-hover:#262640;
  --border:       #313244;
  --border-hl:    #45475a;
  --text:         #cdd6f4;
  --text-dim:     #a6adc8;
  --text-muted:   #6c7086;
  --accent:       #89b4fa;
  --accent-dim:   #74a8f7;
  --accent-red:   #f38ba8;
  --accent-green: #a6e3a1;
  --accent-blue:  #89b4fa;
  --green:        #a6e3a1;
  --red:          #f38ba8;
  --yellow:       #f9e2af;
  --peach:        #fab387;
  --mauve:        #cba6f7;
  --run-bg:       #264f78;
  --run-bg-hover: #305e8f;
  --run-glow:     rgba(137,180,250,.25);
  --stop-bg:      #48232e;
  --stop-border:  #f38ba8;
  --radius:       6px;
  --radius-lg:    10px;
  --shadow:       0 2px 8px rgba(0,0,0,.35);
  --shadow-lg:    0 8px 40px rgba(0,0,0,.5);
  --transition:   150ms ease;
}

/* ── Light theme ───────────────────────────────────────── */
[data-theme="light"] {
  --bg-base:      #f5f5f7;
  --bg-surface:   #ffffff;
  --bg-overlay:   #eaeaef;
  --bg-mantle:    #f0f0f5;
  --bg-card:      #ffffff;
  --bg-card-hover:#f0f0fa;
  --border:       #d5d5dd;
  --border-hl:    #c0c0cc;
  --text:         #1e1e2e;
  --text-dim:     #444466;
  --text-muted:   #888899;
  --accent:       #3b6fdf;
  --accent-dim:   #2d5bc0;
  --accent-red:   #d73a5a;
  --accent-green: #2a8a44;
  --accent-blue:  #3b6fdf;
  --green:        #2a8a44;
  --red:          #d73a5a;
  --yellow:       #b88600;
  --peach:        #c87020;
  --mauve:        #7c4dff;
  --run-bg:       #3b6fdf;
  --run-bg-hover: #2d5bc0;
  --run-glow:     rgba(59,111,223,.2);
  --stop-bg:      #fde8ec;
  --stop-border:  #d73a5a;
  --shadow:       0 2px 8px rgba(0,0,0,.08);
  --shadow-lg:    0 8px 40px rgba(0,0,0,.12);
}

/* ── Reset ─────────────────────────────────────────────── */
* {
  box-sizing: border-box;
}

html, body, #root {
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: 'Inter', 'Segoe UI', system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
  background: var(--bg-base);
  color: var(--text);
  transition: background 300ms ease, color 300ms ease;
}

/* ════════════════════════════════════════════════════════
   START MENU
   ════════════════════════════════════════════════════════ */
.start-menu-overlay {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-base);
  overflow: auto;
}

.start-menu {
  width: 100%;
  max-width: 820px;
  padding: 48px 32px 56px;
  animation: fadeUp 400ms ease;
}

.start-header {
  text-align: center;
  margin-bottom: 40px;
}

.start-logo {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  border-radius: 16px;
  background: var(--accent);
  color: var(--bg-base);
  margin-bottom: 16px;
}

.start-title {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: -0.5px;
  margin: 0 0 8px;
  color: var(--text);
}

.start-subtitle {
  font-size: 14px;
  color: var(--text-muted);
  margin: 0;
}

.start-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.start-card {
  display: flex;
  gap: 14px;
  align-items: flex-start;
  padding: 20px;
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  cursor: pointer;
  text-align: left;
  transition: background var(--transition), border-color var(--transition),
              box-shadow var(--transition), transform var(--transition);
}
.start-card:hover {
  background: var(--bg-card-hover);
  border-color: var(--card-accent, var(--accent));
  box-shadow: 0 4px 20px rgba(0,0,0,.12);
  transform: translateY(-2px);
}

.start-card-icon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 10px;
  background: var(--bg-overlay);
  color: var(--card-accent, var(--accent));
}

.start-card-body {
  flex: 1;
  min-width: 0;
}

.start-card-title {
  font-size: 15px;
  font-weight: 600;
  margin: 0 0 2px;
  color: var(--text);
}

.start-card-sub {
  font-size: 12px;
  color: var(--text-muted);
  margin: 0 0 6px;
}

.start-card-desc {
  font-size: 12.5px;
  color: var(--text-dim);
  margin: 0;
  line-height: 1.5;
}

.start-card--blank {
  border-style: dashed;
}

/* ── Start menu filter bar ─────────────────────────────── */
.start-filter-bar {
  display: flex;
  gap: 8px;
  justify-content: center;
  margin-bottom: 20px;
}

.start-filter-btn {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  padding: 7px 16px;
  font-size: 12.5px;
  font-weight: 500;
  color: var(--text-dim);
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 20px;
  cursor: pointer;
  transition: background var(--transition), border-color var(--transition),
              color var(--transition), box-shadow var(--transition);
  user-select: none;
}
.start-filter-btn:hover {
  background: var(--bg-card-hover);
  border-color: var(--border-hl);
  color: var(--text);
}
.start-filter-btn--active {
  background: var(--accent);
  color: var(--bg-overlay);
  border-color: var(--accent);
  font-weight: 600;
}
.start-filter-btn--active:hover {
  background: var(--accent-dim);
  border-color: var(--accent-dim);
  color: var(--bg-overlay);
}

/* ── Card badges ───────────────────────────────────────── */
.start-card-badge {
  display: inline-block;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  padding: 2px 8px;
  border-radius: 4px;
  margin-bottom: 6px;
}
.start-card-badge--code {
  background: rgba(137, 180, 250, 0.15);
  color: var(--accent-blue);
}
.start-card-badge--blocks {
  background: rgba(203, 166, 247, 0.15);
  color: var(--mauve);
}

[data-theme="light"] .start-card-badge--code {
  background: rgba(59, 111, 223, 0.1);
}
[data-theme="light"] .start-card-badge--blocks {
  background: rgba(124, 77, 255, 0.1);
}

/* ════════════════════════════════════════════════════════
   APP SHELL
   ════════════════════════════════════════════════════════ */
.app-shell {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

/* ── Header / Toolbar ──────────────────────────────────── */
.toolbar {
  display: flex;
  gap: 6px;
  align-items: center;
  padding: 8px 14px;
  background: var(--bg-surface);
  border-bottom: 1px solid var(--border);
  box-shadow: var(--shadow);
  z-index: 10;
}

.toolbar-logo {
  display: flex;
  align-items: center;
  gap: 6px;
  color: var(--accent);
  margin-right: 10px;
  user-select: none;
}
.toolbar-logo-text {
  font-size: 15px;
  font-weight: 700;
  letter-spacing: -0.3px;
  color: var(--accent);
}
.toolbar-logo-text span {
  color: var(--text-dim);
  font-weight: 400;
}

.toolbar-spacer {
  flex: 1;
}

/* Shared button base */
.tb-btn {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  padding: 6px 12px;
  font-size: 12.5px;
  font-weight: 500;
  color: var(--text);
  background: var(--bg-base);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  cursor: pointer;
  transition: background var(--transition), border-color var(--transition),
              box-shadow var(--transition), color var(--transition);
  user-select: none;
  white-space: nowrap;
}
.tb-btn:hover {
  background: var(--border);
  border-color: var(--border-hl);
}
.tb-btn:active {
  transform: scale(0.97);
}

/* Run button accent */
.tb-btn--run {
  background: var(--run-bg);
  border-color: var(--accent);
  color: #fff;
}
.tb-btn--run:hover {
  background: var(--run-bg-hover);
  box-shadow: 0 0 10px var(--run-glow);
}

/* Stop button */
.tb-btn--stop {
  background: var(--stop-bg);
  border-color: var(--stop-border);
  color: var(--red);
}
.tb-btn--stop:hover:not(:disabled) {
  background: var(--red);
  color: #fff;
}

/* Stop button disabled */
.tb-btn--stop:disabled,
.tb-btn--stop.tb-btn--disabled {
  opacity: 0.4;
  cursor: not-allowed;
  pointer-events: none;
  filter: saturate(0.3);
}

/* Home / Menu button */
.tb-btn--home {
  border-color: var(--border);
  color: var(--text-dim);
}
.tb-btn--home:hover {
  border-color: var(--accent);
  color: var(--accent);
  background: var(--bg-overlay);
}

/* Toolbar separator */
.tb-separator {
  width: 1px;
  height: 20px;
  background: var(--border);
  margin: 0 4px;
  flex-shrink: 0;
}

/* Danger / Reset */
.tb-btn--danger {
  border-color: var(--border-hl);
}
.tb-btn--danger:hover {
  border-color: var(--red);
  color: var(--red);
}

/* Theme toggle */
.tb-btn--theme {
  padding: 6px 8px;
  border-color: transparent;
  background: transparent;
}
.tb-btn--theme:hover {
  background: var(--bg-overlay);
  border-color: var(--border);
}

/* PDF export buttons */
.tb-btn--pdf {
  border-color: var(--border);
  color: var(--text-dim);
}
.tb-btn--pdf:hover {
  border-color: var(--mauve);
  color: var(--mauve);
  background: var(--bg-overlay);
}

/* Mode toggle */
.mode-toggle {
  display: flex;
  background: var(--bg-overlay);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  overflow: hidden;
}
.mode-toggle button {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 5px 14px;
  font-size: 12.5px;
  font-weight: 500;
  color: var(--text-dim);
  background: transparent;
  border: none;
  cursor: pointer;
  transition: background var(--transition), color var(--transition);
}
.mode-toggle button:hover {
  color: var(--text);
  background: var(--border);
}
.mode-toggle .active {
  background: var(--accent) !important;
  color: var(--bg-overlay) !important;
  font-weight: 600;
}
.mode-toggle .mode-toggle--disabled {
  opacity: 0.35;
  cursor: not-allowed;
  pointer-events: none;
  color: var(--text-dim) !important;
  background: var(--bg-overlay) !important;
}

/* ── Main layout ───────────────────────────────────────── */
.main-layout {
  display: flex;
  flex: 1;
  min-height: 0;
}

.editor-pane {
  flex: 1;
  min-width: 0;
  min-height: 0;
  display: flex;
  flex-direction: column;
  border-right: 1px solid var(--border);
}
.canvas-pane {
  flex: 1;
  min-width: 0;
  min-height: 0;
  display: flex;
  flex-direction: column;
  background: var(--bg-overlay);
}

/* ── Pane headers ──────────────────────────────────────── */
.pane-header {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 5px 12px;
  font-size: 11.5px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  color: var(--text-muted);
  background: var(--bg-surface);
  border-bottom: 1px solid var(--border);
  user-select: none;
}

/* ── Blockly host ──────────────────────────────────────── */
.blockly-host,
.monaco-host {
  width: 100%;
  flex: 1;
  min-height: 0;
}

.blockly-host {
  position: relative;
  overflow: hidden;
  background: var(--bg-base);
}

/* ── Split editor layout (custom/blank projects) ────────── */
.editor-split-top {
  flex: 2;
  min-height: 0;
  display: flex;
  flex-direction: column;
}
.editor-split-bottom {
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
  border-top: 1px solid var(--border);
}
.pane-header--code-preview {
  background: var(--bg-overlay);
  font-size: 10.5px;
  letter-spacing: 0.6px;
}

/* ── Canvas / GlowScript host ──────────────────────────── */
.canvas-wrap {
  flex: 1;
  overflow: auto;
  background: var(--bg-overlay);
  display: flex;
  flex-direction: column;
}

.glow-host {
  width: 100%;
  flex: 1;
  min-height: 300px;
}

/* Override GlowScript's white background for its injected canvas */
.glow-host div {
  background: transparent !important;
}

/* ── Console / output bar ──────────────────────────────── */
.console-bar {
  padding: 6px 12px;
  font-size: 11px;
  font-family: ui-monospace, 'Cascadia Code', 'Fira Code', 'Consolas', monospace;
  color: var(--text-muted);
  background: var(--bg-surface);
  border-top: 1px solid var(--border);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
}
.console-bar--error {
  color: var(--red);
}
.console-bar--success {
  color: var(--green);
}

/* ── Fallback panels ───────────────────────────────────── */
.fallback-panel {
  padding: 16px;
  color: var(--red);
  font-size: 13px;
}

.text-fallback {
  width: 100%;
  height: 100%;
  border: none;
  padding: 14px;
  font-family: ui-monospace, 'Cascadia Code', 'Fira Code', 'Consolas', monospace;
  font-size: 13.5px;
  line-height: 1.6;
  background: var(--bg-base);
  color: var(--text);
  resize: none;
}
.text-fallback:focus {
  outline: none;
}

/* ── Status bar (bottom) ───────────────────────────────── */
.status-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 3px 14px;
  font-size: 11px;
  color: var(--text-muted);
  background: var(--bg-surface);
  border-top: 1px solid var(--border);
  user-select: none;
}
.status-bar span {
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

/* ── Scrollbar styling ─────────────────────────────────── */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--bg-surface);
}
::-webkit-scrollbar-thumb {
  background: var(--border-hl);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--text-muted);
}

/* ── Animations ────────────────────────────────────────── */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(4px); }
  to   { opacity: 1; transform: translateY(0); }
}
@keyframes fadeUp {
  from { opacity: 0; transform: translateY(16px); }
  to   { opacity: 1; transform: translateY(0); }
}

.editor-pane,
.canvas-pane {
  animation: fadeIn 250ms ease;
}

/* ── Blockly custom overrides ──────────────────────────── */
.blocklyToolboxDiv {
  background: var(--bg-surface) !important;
  border-right: 1px solid var(--border) !important;
  padding: 6px 0 !important;
  width: 190px !important;
}
.blocklyTreeRow {
  height: auto !important;
  padding: 6px 12px 6px 16px !important;
  margin: 1px 6px !important;
  border-radius: 6px !important;
  line-height: 1.4 !important;
  transition: background 120ms ease !important;
}
.blocklyTreeRow:hover {
  background: var(--bg-overlay) !important;
}
.blocklyTreeSelected {
  background: var(--accent) !important;
  border-radius: 6px !important;
}
.blocklyTreeLabel {
  font-size: 12.5px !important;
  font-family: 'Inter', system-ui, sans-serif !important;
  font-weight: 500 !important;
  color: var(--text) !important;
}
.blocklyTreeSelected .blocklyTreeLabel {
  color: #fff !important;
  font-weight: 600 !important;
}
.blocklyTreeSeparator {
  border-bottom: 1px solid var(--border) !important;
  margin: 6px 14px !important;
  height: 0 !important;
}
.blocklyFlyoutBackground {
  fill: var(--bg-surface) !important;
  fill-opacity: 0.98 !important;
  stroke: var(--border) !important;
  stroke-width: 1 !important;
}
.blocklyMainBackground {
  fill: var(--bg-base) !important;
}
.blocklyScrollbarHandle {
  fill: var(--border-hl) !important;
  rx: 4 !important;
  ry: 4 !important;
}
.blocklyScrollbarBackground:hover + .blocklyScrollbarHandle,
.blocklyScrollbarHandle:hover {
  fill: var(--text-muted) !important;
}

/* Blockly block styling */
.blocklySelected > .blocklyPath {
  stroke: var(--accent) !important;
  stroke-width: 2px !important;
}
.blocklyDragging > .blocklyPath {
  stroke: var(--accent) !important;
  stroke-width: 2.5px !important;
  filter: drop-shadow(0 4px 12px rgba(0,0,0,.3)) !important;
}

/* Blockly text inputs */
.blocklyHtmlInput {
  font-family: ui-monospace, 'Cascadia Code', 'Fira Code', monospace !important;
  font-size: 12px !important;
  border-radius: 4px !important;
  border: 2px solid var(--accent) !important;
  padding: 2px 4px !important;
}

/* Blockly tooltips */
.blocklyTooltipDiv {
  font-family: 'Inter', system-ui, sans-serif !important;
  font-size: 12px !important;
  background: var(--bg-surface) !important;
  color: var(--text) !important;
  border: 1px solid var(--border) !important;
  border-radius: 6px !important;
  padding: 6px 10px !important;
  box-shadow: var(--shadow-lg) !important;
}

/* Blockly context menus */
.blocklyContextMenu {
  border-radius: var(--radius) !important;
  border: 1px solid var(--border) !important;
  box-shadow: var(--shadow-lg) !important;
  background: var(--bg-surface) !important;
}
.blocklyContextMenu .goog-menuitem {
  font-family: 'Inter', system-ui, sans-serif !important;
  font-size: 12.5px !important;
  padding: 6px 14px !important;
}

/* Grid dots */
.blocklyMainBackground + .blocklyGridPattern line {
  stroke: var(--border) !important;
}

/* Blockly toolbox icon dots */
.blocklyTreeIcon {
  display: none !important;
}

/* Light theme Blockly adjustments */
[data-theme="light"] .blocklyToolboxDiv {
  background: var(--bg-surface) !important;
}
[data-theme="light"] .blocklyFlyoutBackground {
  fill: #f0f0f5 !important;
}
[data-theme="light"] .blocklyMainBackground {
  fill: #f5f5f7 !important;
}
[data-theme="light"] .blocklyTooltipDiv {
  background: #ffffff !important;
  color: #1e1e2e !important;
}

/* ── Responsive ────────────────────────────────────────── */
@media (max-width: 800px) {
  .main-layout {
    flex-direction: column;
  }
  .editor-pane {
    border-right: none;
    border-bottom: 1px solid var(--border);
    max-height: 55vh;
  }
  .start-grid {
    grid-template-columns: 1fr;
  }
  .start-menu {
    padding: 32px 16px 40px;
  }
}

/* ════════════════════════════════════════════════════════
   UI POLISH IMPROVEMENTS
   ════════════════════════════════════════════════════════ */

/* Toolbar — subtle gradient top edge */
.toolbar {
  background: linear-gradient(180deg, var(--bg-surface) 0%, color-mix(in srgb, var(--bg-surface) 96%, var(--accent) 4%) 100%);
  border-bottom: 1px solid var(--border);
}

/* Help button */
.tb-btn--help {
  border-color: var(--border);
  color: var(--text-dim);
}
.tb-btn--help:hover {
  border-color: var(--mauve);
  color: var(--mauve);
  background: color-mix(in srgb, var(--bg-overlay) 100%, var(--mauve) 5%);
}

/* Pane headers — slightly more prominent */
.pane-header {
  background: linear-gradient(180deg, var(--bg-surface) 0%, var(--bg-overlay) 100%);
  border-bottom: 1px solid var(--border);
  letter-spacing: 0.9px;
}

/* Run button — subtle glow animation when running */
@keyframes runPulse {
  0%, 100% { box-shadow: 0 0 0 0 var(--run-glow); }
  50%       { box-shadow: 0 0 12px 3px var(--run-glow); }
}
.tb-btn--run {
  background: linear-gradient(135deg, var(--run-bg), color-mix(in srgb, var(--run-bg) 80%, var(--accent)));
}
.tb-btn--run:hover {
  animation: runPulse 1.5s ease infinite;
}

/* Start menu improvements */
.start-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
  gap: 14px;
}

.start-help-btn {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  margin-top: 14px;
  padding: 7px 16px;
  font-size: 12.5px;
  font-weight: 500;
  color: var(--text-dim);
  background: transparent;
  border: 1px solid var(--border);
  border-radius: 20px;
  cursor: pointer;
  transition: color var(--transition), border-color var(--transition),
              background var(--transition);
}
.start-help-btn:hover {
  color: var(--mauve);
  border-color: var(--mauve);
  background: color-mix(in srgb, var(--bg-overlay) 100%, var(--mauve) 5%);
}

/* Status bar improvements */
.status-bar {
  gap: 8px;
  font-size: 11.5px;
}
.status-bar .console-bar {
  border: none;
  padding: 0;
  background: transparent;
  font-size: 11.5px;
}

/* ════════════════════════════════════════════════════════
   HELP PAGE
   ════════════════════════════════════════════════════════ */

.help-overlay {
  position: fixed;
  inset: 0;
  z-index: 1000;
  background: var(--bg-overlay);
  display: flex;
  flex-direction: column;
  animation: fadeIn 180ms ease;
}

.help-shell {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

/* Top bar */
.help-topbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 24px;
  background: var(--bg-surface);
  border-bottom: 2px solid var(--accent);
  flex-shrink: 0;
}
.help-topbar-left {
  display: flex;
  align-items: center;
  gap: 10px;
}
.help-topbar-icon {
  font-size: 22px;
  line-height: 1;
}
.help-topbar-title {
  margin: 0;
  font-size: 17px;
  font-weight: 700;
  letter-spacing: -0.3px;
  color: var(--text);
}
.help-close-btn {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  padding: 7px 16px;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-dim);
  background: var(--bg-base);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  cursor: pointer;
  transition: all var(--transition);
}
.help-close-btn:hover {
  background: var(--red);
  border-color: var(--red);
  color: #fff;
}

/* Body: sidebar + content */
.help-body {
  display: flex;
  flex: 1;
  min-height: 0;
}

/* Sidebar */
.help-sidebar {
  width: 210px;
  flex-shrink: 0;
  background: var(--bg-surface);
  border-right: 1px solid var(--border);
  padding: 16px 0 24px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}
.help-sidebar-label {
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--text-muted);
  padding: 0 16px 10px;
  margin: 0;
}
.help-nav-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 9px 16px;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-dim);
  background: transparent;
  border: none;
  border-left: 2px solid transparent;
  cursor: pointer;
  text-align: left;
  transition: color var(--transition), background var(--transition),
              border-color var(--transition);
}
.help-nav-item:hover {
  color: var(--text);
  background: var(--bg-overlay);
}
.help-nav-item--active {
  color: var(--accent);
  background: color-mix(in srgb, var(--bg-overlay) 100%, var(--accent) 5%);
  border-left-color: var(--accent);
  font-weight: 600;
}
.help-nav-emoji {
  font-size: 14px;
  flex-shrink: 0;
  width: 20px;
  text-align: center;
}

/* Main content */
.help-content {
  flex: 1;
  overflow-y: auto;
  padding: 0 40px 60px;
  max-width: 900px;
  scroll-behavior: smooth;
}

.help-section-anchor {
  position: relative;
  top: -80px;
  visibility: hidden;
  pointer-events: none;
}

/* Sections */
.help-section {
  padding: 40px 0 20px;
  border-bottom: 1px solid var(--border);
}
.help-section:last-of-type {
  border-bottom: none;
}
.help-h2 {
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -0.3px;
  margin: 0 0 14px;
  color: var(--text);
}
.help-h3 {
  font-size: 15px;
  font-weight: 600;
  margin: 24px 0 10px;
  color: var(--text);
  padding-bottom: 6px;
  border-bottom: 1px solid var(--border);
}
.help-section p {
  font-size: 14px;
  line-height: 1.7;
  color: var(--text-dim);
  margin: 0 0 12px;
}
.help-list {
  font-size: 14px;
  line-height: 1.7;
  color: var(--text-dim);
  margin: 0 0 14px;
  padding-left: 20px;
}
.help-list li {
  margin-bottom: 6px;
}

/* Inline code */
.help-inline-code {
  font-family: ui-monospace, 'Cascadia Code', 'Fira Code', 'Consolas', monospace;
  font-size: 12.5px;
  background: var(--bg-overlay);
  border: 1px solid var(--border);
  border-radius: 4px;
  padding: 1px 5px;
  color: var(--accent);
}

/* Code block */
.help-code-block {
  background: var(--bg-overlay);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 14px 16px;
  overflow-x: auto;
  margin: 10px 0;
}
.help-code-block code {
  font-family: ui-monospace, 'Cascadia Code', 'Fira Code', 'Consolas', monospace;
  font-size: 12.5px;
  line-height: 1.65;
  color: var(--text);
  white-space: pre;
  display: block;
}

/* Tags */
.help-tag {
  display: inline-block;
  font-size: 10.5px;
  font-weight: 600;
  letter-spacing: 0.4px;
  padding: 2px 8px;
  border-radius: 4px;
  vertical-align: middle;
}
.help-tag--blue   { background: rgba(137,180,250,.15); color: var(--accent-blue); }
.help-tag--purple { background: rgba(203,166,247,.15); color: var(--mauve); }
.help-tag--green  { background: rgba(166,227,161,.15); color: var(--green); }
.help-tag--yellow { background: rgba(249,226,175,.15); color: var(--yellow); }
.help-tag--teal   { background: rgba(148,226,213,.15); color: #94e2d5; }
.help-tag--red    { background: rgba(243,139,168,.15); color: var(--red); }

[data-theme="light"] .help-tag--blue   { background: rgba(59,111,223,.1);  color: var(--accent-blue); }
[data-theme="light"] .help-tag--purple { background: rgba(124,77,255,.1);  color: var(--mauve); }
[data-theme="light"] .help-tag--green  { background: rgba(42,138,68,.1);   color: var(--green); }
[data-theme="light"] .help-tag--yellow { background: rgba(184,134,0,.1);   color: var(--yellow); }
[data-theme="light"] .help-tag--teal   { background: rgba(0,150,130,.1);   color: #007b6e; }
[data-theme="light"] .help-tag--red    { background: rgba(215,58,90,.1);   color: var(--red); }

/* Notes */
.help-note {
  display: flex;
  gap: 10px;
  padding: 12px 14px;
  border-radius: var(--radius);
  border-left: 3px solid;
  font-size: 13.5px;
  line-height: 1.6;
  margin: 14px 0;
  color: var(--text-dim);
}
.help-note--info    { background: rgba(137,180,250,.08); border-color: var(--accent-blue); }
.help-note--tip     { background: rgba(166,227,161,.08); border-color: var(--green); }
.help-note--warning { background: rgba(249,226,175,.08); border-color: var(--yellow); }

[data-theme="light"] .help-note--info    { background: rgba(59,111,223,.06); }
[data-theme="light"] .help-note--tip     { background: rgba(42,138,68,.06);  }
[data-theme="light"] .help-note--warning { background: rgba(184,134,0,.06);  }

/* Keyboard */
.help-kbd {
  display: inline-block;
  font-family: ui-monospace, 'Cascadia Code', monospace;
  font-size: 11px;
  padding: 2px 7px;
  background: var(--bg-overlay);
  border: 1px solid var(--border-hl);
  border-radius: 4px;
  border-bottom-width: 2px;
  color: var(--text);
}

/* Tables */
.help-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
  margin: 12px 0 18px;
  border-radius: var(--radius);
  overflow: hidden;
  border: 1px solid var(--border);
}
.help-table th {
  background: var(--bg-surface);
  text-align: left;
  padding: 9px 12px;
  font-size: 11.5px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--text-muted);
  border-bottom: 1px solid var(--border);
}
.help-table td {
  padding: 9px 12px;
  border-bottom: 1px solid var(--border);
  color: var(--text-dim);
  line-height: 1.5;
  vertical-align: top;
}
.help-table tr:last-child td {
  border-bottom: none;
}
.help-table tr:hover td {
  background: var(--bg-surface);
}

/* Architecture grid */
.help-arch-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  margin: 14px 0;
}
.help-arch-box {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: 16px;
  border-top: 3px solid;
}
.help-arch-box p {
  font-size: 12.5px;
  margin: 6px 0 0;
  color: var(--text-muted);
}
.help-arch-box strong {
  font-size: 13px;
  color: var(--text);
}
.help-arch-box--blue   { border-top-color: var(--accent-blue); }
.help-arch-box--purple { border-top-color: var(--mauve); }
.help-arch-box--green  { border-top-color: var(--green); }

/* Block reference rows */
.help-block-table {
  margin: 10px 0 18px;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  overflow: hidden;
}
.help-block-row {
  display: grid;
  grid-template-columns: 180px 1fr;
  border-bottom: 1px solid var(--border);
}
.help-block-row:last-child {
  border-bottom: none;
}
.help-block-name {
  padding: 12px 14px;
  font-family: ui-monospace, 'Cascadia Code', monospace;
  font-size: 12px;
  color: var(--accent);
  font-weight: 600;
  background: var(--bg-surface);
  border-right: 1px solid var(--border);
  display: flex;
  align-items: flex-start;
  word-break: break-word;
}
.help-block-desc {
  padding: 12px 14px;
  font-size: 13px;
  line-height: 1.6;
  color: var(--text-dim);
}
.help-block-desc p {
  margin: 0 0 6px;
  font-size: 13px;
}

/* Equation box */
.help-equation {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-left: 3px solid var(--mauve);
  border-radius: var(--radius);
  padding: 14px 16px;
  margin: 12px 0;
}
.help-equation p {
  margin: 4px 0;
  font-size: 13.5px;
  color: var(--text-dim);
}
.help-eq {
  font-family: ui-monospace, 'Cascadia Code', monospace;
  font-size: 13px;
  color: var(--mauve);
  background: rgba(203,166,247,.08);
  padding: 1px 6px;
  border-radius: 4px;
}
[data-theme="light"] .help-eq {
  background: rgba(124,77,255,.08);
}

/* Lesson cards */
.help-lesson-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 12px;
  margin: 14px 0;
}
.help-lesson-card {
  display: flex;
  gap: 12px;
  padding: 16px;
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  transition: border-color var(--transition), transform var(--transition);
}
.help-lesson-card:hover {
  border-color: var(--accent);
  transform: translateY(-2px);
}
.help-lesson-num {
  flex-shrink: 0;
  width: 28px;
  height: 28px;
  background: var(--accent);
  color: var(--bg-overlay);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
}
.help-lesson-body strong {
  font-size: 13.5px;
  color: var(--text);
  display: block;
  margin-bottom: 6px;
}
.help-lesson-body p {
  font-size: 12.5px;
  color: var(--text-muted);
  margin: 0 0 8px;
  line-height: 1.55;
}

/* Footer */
.help-footer {
  margin-top: 40px;
  padding: 20px 0;
  font-size: 12px;
  color: var(--text-muted);
  text-align: center;
  border-top: 1px solid var(--border);
}

/* Responsive help page */
@media (max-width: 800px) {
  .help-sidebar {
    display: none;
  }
  .help-content {
    padding: 0 20px 40px;
  }
  .help-arch-grid {
    grid-template-columns: 1fr;
  }
  .help-block-row {
    grid-template-columns: 1fr;
  }
  .help-block-name {
    border-right: none;
    border-bottom: 1px solid var(--border);
  }
}

